"use client";

import { useState, useEffect, useMemo, useRef } from "react";
import { ConnectButton } from "@rainbow-me/rainbowkit";
import { useAccount, useReadContract, useWriteContract, useWaitForTransactionReceipt } from "wagmi";
import { ethers } from "ethers";
import { MentalHealthSurveyAddresses } from "@/abi/MentalHealthSurveyAddresses";
import { MentalHealthSurveyABI } from "@/abi/MentalHealthSurveyABI";
import { useFhevm } from "@/fhevm/useFhevm";
import { useEthersSigner } from "@/hooks/useEthersSigner";
import { useInMemoryStorage } from "@/hooks/useInMemoryStorage";
import { FhevmDecryptionSignature } from "@/fhevm/FhevmDecryptionSignature";
import { FhevmType } from "@/fhevm/fhevmTypes";

type SurveyForm = {
  stressLevel: string;
  anxietyLevel: string;
  moodScore: string;
  sleepQuality: string;
  energyLevel: string;
};

type DecryptedSurvey = {
  stressLevel: number;
  anxietyLevel: number;
  moodScore: number;
  sleepQuality: number;
  energyLevel: number;
  timestamp: number;
};

export function MentalHealthSurveyApp() {
  const { address, isConnected, chainId } = useAccount();
  const { writeContract, data: hash, isPending } = useWriteContract();
  const { isLoading: isConfirming, isSuccess: isConfirmed } = useWaitForTransactionReceipt({ hash });

  const [form, setForm] = useState<SurveyForm>({
    stressLevel: "",
    anxietyLevel: "",
    moodScore: "",
    sleepQuality: "",
    energyLevel: "",
  });

  const [surveyCount, setSurveyCount] = useState<bigint>(0n);
  const [decryptedSurveys, setDecryptedSurveys] = useState<DecryptedSurvey[]>([]);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  const contractAddress = useMemo(() => {
    if (!chainId) return undefined;
    const addr = MentalHealthSurveyAddresses[chainId.toString() as keyof typeof MentalHealthSurveyAddresses];
    return addr?.address;
  }, [chainId]);

  const { data: count, error: readError } = useReadContract({
    address: contractAddress,
    abi: MentalHealthSurveyABI.abi,
    functionName: "getSurveyCount",
    args: address ? [address] : undefined,
    query: {
      enabled: !!address && !!contractAddress && contractAddress !== "0x0000000000000000000000000000000000000000",
      retry: 1,
    },
  });

  useEffect(() => {
    if (readError) {
      console.error("Contract read error:", readError);
      setMessage(`Error reading contract: ${readError.message}`);
    }
  }, [readError]);

  useEffect(() => {
    if (count !== undefined) {
      setSurveyCount(count);
    }
  }, [count]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!address || !contractAddress) {
      setMessage("Please connect your wallet");
      return;
    }

    setLoading(true);
    setMessage("");

    try {
      // Note: In a real implementation, you would encrypt these values using FHEVM
      // For now, this is a placeholder that shows the structure
      // The actual encryption would happen client-side before sending to the contract
      
      setMessage("Encryption and submission in progress...");
      // This would need to be implemented with actual FHEVM encryption
      setMessage("Please implement FHEVM encryption before submitting");
    } catch (error: any) {
      setMessage(error?.message || "Failed to submit survey");
    } finally {
      setLoading(false);
    }
  };

  const handleDecrypt = async (index: number) => {
    if (!address || !contractAddress) {
      setMessage("Please connect your wallet");
      return;
    }

    setLoading(true);
    setMessage("");

    try {
      // Note: In a real implementation, you would decrypt using FHEVM
      // This is a placeholder showing the structure
      setMessage("Decryption in progress...");
      // This would need to be implemented with actual FHEVM decryption
      setMessage("Please implement FHEVM decryption");
    } catch (error: any) {
      setMessage(error?.message || "Failed to decrypt survey");
    } finally {
      setLoading(false);
    }
  };

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setForm((f) => ({ ...f, [name]: value }));
  };

  return (
    <div className="w-full max-w-4xl mx-auto px-4">
      <div className="flex justify-end mb-6">
        <ConnectButton />
      </div>

      {!isConnected ? (
        <div className="bg-white rounded-lg shadow-lg p-8 text-center">
          <h2 className="text-2xl font-semibold mb-4">Connect Your Wallet</h2>
          <p className="text-gray-600 mb-6">
            Please connect your wallet to start using the Encrypted Mental Health Survey
          </p>
          <ConnectButton />
        </div>
      ) : (
        <>
          <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">
              Submit Mental Health Survey
            </h2>
            <p className="text-gray-600 mb-6">
              Your responses are encrypted and stored securely. Only you can decrypt them.
            </p>

            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Stress Level (0-100)
                </label>
                <input
                  type="number"
                  name="stressLevel"
                  min="0"
                  max="100"
                  required
                  value={form.stressLevel}
                  onChange={onChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Enter stress level (0-100)"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Anxiety Level (0-100)
                </label>
                <input
                  type="number"
                  name="anxietyLevel"
                  min="0"
                  max="100"
                  required
                  value={form.anxietyLevel}
                  onChange={onChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Enter anxiety level (0-100)"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Mood Score (0-100)
                </label>
                <input
                  type="number"
                  name="moodScore"
                  min="0"
                  max="100"
                  required
                  value={form.moodScore}
                  onChange={onChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Enter mood score (0-100)"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Sleep Quality (0-100)
                </label>
                <input
                  type="number"
                  name="sleepQuality"
                  min="0"
                  max="100"
                  required
                  value={form.sleepQuality}
                  onChange={onChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Enter sleep quality (0-100)"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Energy Level (0-100)
                </label>
                <input
                  type="number"
                  name="energyLevel"
                  min="0"
                  max="100"
                  required
                  value={form.energyLevel}
                  onChange={onChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Enter energy level (0-100)"
                />
              </div>

              <button
                type="submit"
                disabled={loading || isPending}
                className="w-full bg-blue-600 text-white py-3 px-6 rounded-lg font-semibold hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading || isPending ? "Submitting..." : "Submit Encrypted Survey"}
              </button>
            </form>
          </div>

          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-2xl font-semibold mb-4 text-gray-800">
              Survey History
            </h2>
            <p className="text-gray-600 mb-4">
              You have {surveyCount.toString()} survey{surveyCount !== 1n ? "s" : ""} stored
            </p>

            {surveyCount > 0n && (
              <div className="space-y-4">
                {Array.from({ length: Number(surveyCount) }).map((_, index) => (
                  <div
                    key={index}
                    className="border border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors"
                  >
                    <div className="flex justify-between items-center">
                      <div>
                        <h3 className="font-semibold text-gray-800">
                          Survey #{index + 1}
                        </h3>
                        <p className="text-sm text-gray-600">
                          Click to decrypt and view
                        </p>
                      </div>
                      <button
                        onClick={() => handleDecrypt(index)}
                        disabled={loading}
                        className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 transition-colors disabled:opacity-50"
                      >
                        Decrypt
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            )}

            {surveyCount === 0n && (
              <p className="text-gray-500 text-center py-8">
                No surveys submitted yet. Submit your first survey above!
              </p>
            )}
          </div>

          {message && (
            <div className="mt-4 p-4 bg-blue-50 border border-blue-200 rounded-lg">
              <p className="text-blue-800">{message}</p>
            </div>
          )}
        </>
      )}
    </div>
  );
}

